  ._1Lxpd{
    width:100% !important;
    display: block
  }
  ._3PfrK{
    width: 380px !important;
    height: 380px !important
  }
  ._2hdkJ{
    width: 250px !important;
    height: 250px !important;
    top:17% !important;
    left:17% !important;

  }
  ._3_sBP{
    width: 100%;

  }
  ._TWsuu{
    width: 90%;
